/* ---------------------------------------------------------
	
	* Filename:				default.css
	* Description: 			sets basic grids and standard formstyles
	* Version:				8.0.0 (2015-11-19)
	* Author:				Timo Breumelhof
	
	== INDEX ===============================================
	
	$__generic classes		classes used globally, should be at bottom to prevent semantic inheritance
	
------------------------------------------------------------*/
.dnn_mact { padding-left: 0; }

/* Module titles */
.Head { display: inline-block; margin-bottom: 6px; }

/*-------------------------------------*/
/* MESSAGE STYLES */
/*-------------------------------------*/
.dnnFormMessage { display: block; padding: 17px 18px; margin-bottom: 18px; border: 1px solid rgba(2, 139, 255, 0.2); /* blue */ background: rgba(2, 139, 255, 0.15); /* blue */ border-radius: 3px; max-width: 980px; }

.dnnFormMessage.dnnFormError, .dnnFormMessage.dnnFormValidationSummary { background-color: rgba(255, 0, 0, 0.15); /* red */ border-color: rgba(255, 0, 0, 0.2); /* red */ }

.dnnFormMessage.dnnFormWarning { background-color: rgba(255, 255, 0, 0.15); /* yellow */ border-color: #CDB21F; /* yellow */ }

.dnnFormMessage.dnnFormSuccess { background-color: rgba(0, 255, 0, 0.15); /* green */ border-color: rgba(0, 255, 0, 0.5); /* green */ }

/*-------------------------------------*/
/* TABLE STYLES */
/*-------------------------------------*/
.dnnTableHeader { display: block; padding: 15px; background: none repeat scroll 0 0 #E3E3E5; }

.dnnTableHeader a, .dnnTableHeader input, .dnnTableHeader select { margin: 0; }

.dnnTableDisplay { margin-bottom: 18px; border: 1px solid #d1d1d1; border-left: 0; border-bottom: 0; }

.dnnTableDisplay td, .dnnTableDisplay th { padding: 5px 9px; border-bottom: 1px solid #d1d1d1; }

.dnnTableDisplay th { padding: 0; border-left: 1px solid #d1d1d1; box-shadow: 1px 0px 0px 0px rgba(255, 255, 255, 0.9); }

.dnnTableDisplay th > span, .dnnTableDisplay th > a { display: block; padding: 5px 9px; }

.dnnTableDisplay td:first-child { border-left: 1px solid #cfcfcf; }

.dnnTableDisplay label { display: inline; padding-left: 5px; }

.dnnTableDisplay caption { display: none; }

/* thead styles */
.dnnTableDisplay th { background: linear-gradient(to bottom, #f5f5f5 0%, #dfdfdf 100%); /* W3C */ text-align: left; text-shadow: 1px 1px 0px rgba(255, 255, 255, 0.8); color: #333; }

.dnnTableDisplay th a, .dnnTableDisplay th label { font-weight: normal; color: #333; text-decoration: none; }

.dnnTableDisplay th a { padding: 8px 9px; }

.dnnTableDisplay th a:hover { background: #fefefe; /* Old browsers */ background: linear-gradient(to bottom, #fefefe 0%, #e8e8e8 100%); /* W3C */ color: #333; }

.dnnTableDisplay th a:active { border-color: #999; background: #c6c6c6; /* Old browsers */ background: linear-gradient(to bottom, #c6c6c6 0%, #f3f3f3 100%); /* W3C */ box-shadow: inset 0px 2px 3px 0px rgba(0, 0, 0, 0.3); }

/* tbody styles */
.dnnTableDisplay tr:nth-child(2n) td { background: rgba(0, 0, 0, 0.04); }

.dnnTableDisplay tr:hover td { background: rgba(2, 139, 255, 0.15); /* blue */ }

/* tfooter styles */
.dnnTableDisplay tfoot tr:hover td { background: none; }

/* table filter styles */
.dnnTableFilter { margin-bottom: 18px; background: rgba(0, 0, 0, 0.04); }

.dnnTableFilter .dnnTableDisplay { margin-bottom: 0; }

.dnnFilterSet { padding: 18px; }

.dnnFilterSet label { display: inline-block; }

.dnnFilterSet input, .dnnFilterSet select { display: inline-block; margin: 0 5px; }

/* table filter vertical styles */
.verticalFilter .dnnFilterSet { float: left; width: 20%; padding: 2% 18px; }

.verticalFilter .dnnTableDisplay { float: right; width: 75%; }

.verticalFilter input[type=search], .verticalFilter select, .verticalFilter input[type=text] { margin: 0; width: 100%; box-sizing: border-box; }

/*-------------------------------------*/
/* PAGINATION & RESULT STYLES */
/*-------------------------------------*/
.dnnResults { float: left; margin-bottom: 0; }

.dnnPagination { float: right; margin: 0; padding: 0; }

.dnnPagination li { float: left; list-style: none; }

.dnnPagination li a, .dnnPagination li span { display: inline-block; padding: 0 5px; text-decoration: none; color: #333; }

.dnnPagination li a:hover { text-decoration: underline; color: #222; }

.dnnPagination li span { color: #222; text-decoration: underline; }

/*-------------------------------------*/
/* FORM STYLES */
/*-------------------------------------*/
.dnnFormItem input[type="text"], .dnnFormItem select, .dnnFormItem textarea, .dnnFormItem input[type="email"], .dnnFormItem input[type="search"], .dnnFormItem input[type="password"] { margin-bottom: 18px; padding: 8px; background: #ffffff; border: 1px solid #c9c9c9; border-radius: 3px; box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 0.8), inset 0px 1px 2px 0px rgba(0, 0, 0, 0.1); color: #333; font-size: 12px; }

.dnnFormItem input.aspNetDisabled, .dnnFormItem textarea.aspNetDisabled { background: #ebebeb; box-shadow: none; border-color: #a7a7a7; border-width: 1px; }

.aspNetDisabled, a.aspNetDisabled, .dnnDisabled, a.dnnDisabled { color: #ccc; cursor: not-allowed; text-decoration: none; }

/* Active and Focus States */
.dnnFormItem input[type="text"]:active, .dnnFormItem input[type="text"]:focus, .dnnFormItem input[type="password"]:focus, .dnnFormItem input[type="password"]:active, .dnnFormItem input[type="email"]:active, .dnnFormItem input[type="email"]:focus, .dnnFormItem select:active, .dnnFormItem select:focus, .dnnFormItem textarea:active, .dnnFormItem textarea:focus, .dnnFormItem input[type="search"]:active, .dnnFormItem input[type="search"]:focus { background: #fff; border: 1px solid rgba(2, 139, 255, 0.5); /* blue */ box-shadow: 0px 0px 3px 0px rgba(2, 139, 255, 0.4); color: #333; }

/* Required Fields */
.required label:after, legend.required:after { display: inline-block; margin-top: -2px; margin-left: 9px; padding: 1px 9px; border: 1px solid rgba(0, 0, 0, 0.1); border-radius: 3px; background: rgba(0, 0, 0, 0.03); content: 'Required'; font-weight: normal; font-family: "Trebuchet MS", Arial, Helvetica, sans-serif; font-size: 11px; font-style: italic; color: #777; }

/* Error Styles */
.error label, legend.error { color: rgba(255, 0, 0, 0.9); /* opaque red*/ }

.error input[type="text"], .error select, .error textarea, .error input[type="email"], .error input[type="search"] { border: 1px solid rgba(255, 0, 0, 0.6); /* opaque red*/ color: rgba(255, 0, 0, 0.7); /* opaque red*/ }

/* 1. Display hand cursor for clickable form elements 2. Allow styling of clickable form elements in iOS 3. Correct inner spacing displayed oddly in IE7 (doesn't effect IE6) */
.dnnFormItem button, .dnnFormItem input[type="button"], .dnnFormItem input[type="reset"], .dnnFormItem input[type="submit"], .dnnPrimaryAction, .dnnSecondaryAction, .dnnTertiaryAction, ul.dnnAdminTabNav li a, .dnnLogin .LoginTabGroup span { display: inline-block; padding: 9px 9px; margin-bottom: 9px; cursor: pointer; min-width: 75px; *overflow: visible; border: 1px solid #c2c2c2; background: #eaeaea; border-radius: 3px; color: #333; font-weight: bold; text-decoration: none; text-align: center; }

.dnnFormItem button, .dnnFormItem input[type="button"], .dnnFormItem input[type="reset"], .dnnFormItem input[type="submit"], .dnnPrimaryAction, .dnnSecondaryAction, .dnnTertiaryAction { padding: 6px 6px; }

/* Primary Action */
.dnnPrimaryAction, .dnnFormItem input[type="submit"], a.dnnPrimaryAction { border-color: #0D569E; /*dark blue*/ background: #3792ED; background: linear-gradient(to bottom, #358eea 0%, #2170cd 100%); /* W3C */ box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.6), inset 0px 1px 0px 0px rgba(255, 255, 255, 0.4); color: #efefef; text-shadow: 0px -1px 0px #185B93; }

.dnnPrimaryAction:hover, .dnnFormItem input[type="submit"]:hover, a.dnnPrimaryAction:hover { border-color: #0D569E; background: #6cb6f3; /* Old browsers */ background: linear-gradient(to bottom, #6cb6f3 0%, #4387d2 100%); /* W3C */ color: #fff; }

.dnnPrimaryAction:active, .dnnFormItem input[type="submit"]:active, a.dnnPrimaryAction:active { border-color: #0D569E; background: #1f66be; /* Old browsers */ background: linear-gradient(to bottom, #1f66be 0%, #3085e0 100%); /* W3C */ box-shadow: inset 0px 1px 5px 0px rgba(0, 0, 0, 0.4); color: #fff; }

.dnnPrimaryAction[disabled], .dnnPrimaryAction[disabled]:hover, .dnnPrimaryAction[disabled]:active { background: #ccc !important; border: none !important; box-shadow: none !important; color: #aaa !important; text-shadow: none; }

/* Secondary Action */
.dnnFormItem button, .dnnFormItem input[type="button"], .dnnFormItem input[type="reset"], .dnnSecondaryAction, a.dnnSecondaryAction, ul.dnnAdminTabNav li a, .dnnLogin .LoginTabGroup span { background: #f5f5f5; /* Old browsers */ background: linear-gradient(to bottom, #f5f5f5 0%, #dfdfdf 100%); /* W3C */ box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.4), inset 0px 1px 0px 0px white; text-shadow: 0px 1px 0px #ffffff; color: #333; }

.dnnFormItem button:hover, .dnnFormItem input[type="button"]:hover, .dnnFormItem input[type="reset"]:hover, .dnnSecondaryAction:hover, a.dnnSecondaryAction:hover, ul.dnnAdminTabNav li a:hover, .dnnLogin .LoginTabGroup span:hover { background: #fefefe; /* Old browsers */ background: linear-gradient(to bottom, #fefefe 0%, #e8e8e8 100%); /* W3C */ color: #555; }

.dnnFormItem button:active, .dnnFormItem input[type="button"]:active, .dnnFormItem input[type="reset"]:active, .dnnFormItem input[type="reset"]:active, .dnnSecondaryAction:active, a.dnnSecondaryAction:active, ul.dnnAdminTabNav li a:active { border-color: #999; background: #c6c6c6; /* Old browsers */ background: linear-gradient(to bottom, #c6c6c6 0%, #f3f3f3 100%); /* W3C */ box-shadow: inset 0px 1px 5px 0px rgba(0, 0, 0, 0.4); }

/* Fix some old forge modules use .dnnPrimaryAction and .dnnSecondaryAction in wrong way */
span.dnnPrimaryAction > input[type="image"], span.dnnSecondaryAction > input[type="image"] { display: none; }

span.dnnPrimaryAction > a.dnnPrimaryAction, span.dnnSecondaryAction > a.dnnSecondaryAction { display: inline; background: transparent; border: none; box-shadow: none; }

/* Tertiary Action */
.dnnTertiaryAction, a.dnnTertiaryAction { padding: 10px 10px; border: none; background: #eaeaea; color: #333; }

.dnnTertiaryAction:hover, a.dnnTertiaryAction:hover { background: #f2f2f2; }

.dnnTertiaryAction:active, a.dnnTertiaryAction:active { background: #d8d8d8; }

/* Action Button behind input */
.dnnFormItem input[type="text"] + a.dnnPrimaryAction, .dnnFormItem input[type="text"] + a.dnnSecondaryAction, .dnnFormItem input[type="text"] + a.dnnTerriaryAction { margin-left: 5px; }

.dnnFormItem input[type="checkbox"] + span, .dnnFormItem input[type="radio"] + span { margin: 5px; }

/* text style for buttons and link buttons used in the portal admin pages */
/* button style for standard HTML buttons */
.StandardButton { border: 1px solid blue; }

/* Re-set default cursor for disabled elements */
.dnnFormItem button[disabled], .dnnFormItem input[disabled] { cursor: default; opacity: 0.5; }

/* Consistent box sizing and appearance */
.dnnFormItem input[type="checkbox"], .dnnFormItem input[type="radio"] { *width: 13px; *height: 13px; /*why are we using hacks here*/ padding: 0; box-sizing: border-box; }

.dnnFormItem input[type="search"] { box-sizing: content-box; }

.dnnFormItem input[type="search"]::-webkit-search-decoration, .dnnFormItem input[type="search"]::-webkit-search-cancel-button { -webkit-appearance: none; }

/* Remove inner padding and border in FF3/4: h5bp.com/l */
.dnnFormItem button::-moz-focus-inner, .dnnFormItem input::-moz-focus-inner { border: 0; padding: 0; }

/* 1. Remove default vertical scrollbar in IE6/7/8/9 2. Allow only vertical resizing */
.dnnFormItem textarea { overflow: auto; vertical-align: top; resize: vertical; }

.dnnFormItem input:invalid, .dnnFormItem textarea:invalid { background-color: #f0dddd; }

/*-------------------------------------*/
